<template>
  <div class="container">
    <div class="player">
      <div class="player__info">{{ currentWord.en }}</div>

      <div class="player__btns">
        <span class="random-btn" @click="setSort()">{{
          isDes ? "正序" : "乱序"
        }}</span>

        <div class="player__icon icon-random"></div>

        <div class="player__btn" @click="setCurrent(current - 1)">
          <van-icon name="arrow-left" />
        </div>
        <div class="player__btn" @click="onAutoPlay">
          <van-icon :name="playing ? 'pause' : 'play'" />
        </div>
        <div class="player__btn" @click="setCurrent(current + 1)">
          <van-icon name="arrow" />
        </div>

        <div class="player__btn-list" @click="showList = true">
          <van-icon name="bars" />
        </div>
      </div>
    </div>

    <!-- 列表面板 -->
    <van-action-sheet v-model="showList" title="已读">
      <ul>
        <li
          v-for="(word, index) in words"
          :key="word.en"
          :class="{ played: word.played }"
          @click="setCurrent(index)"
        >
          {{ word.en }}
        </li>
      </ul>
    </van-action-sheet>

    <!-- 设置 -->
    <div class="icon-setting" @click="onShow">
      <van-icon name="setting" />
    </div>

    <!-- 设置面板 -->
    <van-action-sheet v-model="show" title="设置">
      <div class="action-content">
        <van-form>
          <van-field label="章节">
            <template #input>
              <van-checkbox-group v-model="setting.days" direction="horizontal">
                <van-checkbox
                  v-for="day in 21"
                  :key="day"
                  :name="day"
                  shape="square"
                  >{{ day }}</van-checkbox
                >
              </van-checkbox-group>
            </template>
          </van-field>
          <van-field label="间隔">
            <template #input>
              <van-radio-group v-model="setting.time" direction="horizontal">
                <van-radio :name="1000">1秒</van-radio>
                <van-radio :name="2000">2秒</van-radio>
                <van-radio :name="3000">3秒</van-radio>
              </van-radio-group>
            </template>
          </van-field>
        </van-form>
        <van-button type="info" @click="onSubmit" block>确认</van-button>
      </div>
    </van-action-sheet>
  </div>
</template>
<script>
import {
  Icon,
  ActionSheet,
  Checkbox,
  CheckboxGroup,
  Cell,
  Radio,
  RadioGroup,
} from "vant";
export default {
  components: {
    [Icon.name]: Icon,
    [ActionSheet.name]: ActionSheet,
    [Cell.name]: Cell,
    [Checkbox.name]: Checkbox,
    [CheckboxGroup.name]: CheckboxGroup,
    [Radio.name]: Radio,
    [RadioGroup.name]: RadioGroup,
  },
  data() {
    return {
      show: false,
      showList: false,
      playing: false,
      audio: null,
      days: [21],
      time: 1000,
      words: [],
      current: 0,
      isDes: true,
      setting: {
        day: [],
        time: 1000,
      },
    };
  },
  computed: {
    currentWord() {
      return this.words[this.current] || {};
    },
  },
  created() {
    this.getWords();
  },
  beforeDestroy() {
    this.audio.pause();
  },
  methods: {
    getRandom(min, max) {
      return Math.floor(Math.random() * (max - min + 1)) + min;
    },
    getRandomWord() {
      const words = [];
      this.words.forEach((item, index) => {
        if (!item.played) {
          words.push({
            ...item,
            index,
          });
        }
      });
      return {
        index: words[this.getRandom(0, words.length - 1)]?.index,
        isFinish: this.words.length && words.length === 0,
      };
    },
    setSort() {
      this.isDes = !this.isDes;
    },
    setCurrent(current) {
      this.current = current;
      setTimeout(() => {
        this.onPlay(this.words[this.current].en);
      }, this.time);
    },
    onShow() {
      this.show = true;
      this.$set(this.setting, "days", this.days);
    },
    onAutoPlay() {
      if (this.playing) {
        this.onPause();
      } else {
        this.onPlay(this.words[this.current].en);
      }
    },
    onPause() {
      this.audio.pause();
      this.audio = null;
      this.playing = false;
    },
    onPlay(word) {
      this.playing = true;
      if (!this.audio) {
        this.audio = new Audio(
          `https://dict.youdao.com/dictvoice?audio=${word}&type=1`
        );
        this.audio.addEventListener(
          "ended",
          () => {
            this.$set(this.words[this.current], "played", true);
            if (this.isDes) {
              if (this.words.length !== this.current + 1) {
                this.setCurrent(this.current + 1);
              } else {
                this.playing = false;
              }
            } else {
              const { index, isFinish } = this.getRandomWord();
              if (isFinish) {
                this.playing = false;
              } else {
                this.setCurrent(index);
              }
            }
          },
          false
        );
      } else {
        this.audio.src = `https://dict.youdao.com/dictvoice?audio=${
          this.words[this.current].en
        }&type=1`;
      }
      this.audio.play();
    },
    getWords() {
      const words = [];
      this.words = [];
      this.days.forEach((day) => {
        const word = require(`./json/${day}.json`);
        words.push(...word);
      });
      this.words = words;
    },
    onSubmit() {
      this.show = false;
      this.current = 0;
      this.days = this.setting.days;
      this.time = this.setting.time;
      this.getWords();
    },
  },
};
</script>
<style lang="scss" scoped>
.container {
  height: 100vh;
  padding-top: 100px;
  // background-color: rgba(0, 0, 0, 0.35);
}
.played {
  background-color: #a9b6c1;
}
li {
  padding: 10px 5px;
}
.player {
  display: flex;
  flex-direction: column;
  align-items: center;
  .random-btn {
    font-size: 14px;
    margin-right: 10px;
  }
  &__info {
    position: relative;
    display: block;
    margin-bottom: 10px;
    width: 200px;
    height: 200px;
    line-height: 200px;
    text-align: center;
    border-radius: 100%;
    background-color: #dbdbdb;
  }
  &__btns {
    display: flex;
    align-items: center;
  }
  &__btn {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    background: rgba($color: #000000, $alpha: 0.5);
  }
  &__btn + &__btn {
    margin-left: 10px;
  }
  &__btn-list {
    width: 20px;
    height: 20px;
    margin-left: 10px;
  }
}

.van-checkbox--horizontal {
  width: 48px;
  margin-bottom: 5px;
}
.icon-setting {
  width: 40px;
  height: 40px;
  display: flex;
  background: #000;
  opacity: 0.3;
  border-radius: 10px;
  position: relative;
  z-index: 1000;
  transition: opacity 0.3s;
  color: #fff;
  font-size: 25px;
  align-items: center;
  justify-content: center;
  position: fixed;
  right: 20px;
  bottom: 20%;
}
</style>
